body{
  margin: 0;
}

*{
  &:before,
  &:after{
    box-sizing: border-box;
  }
}

.client-stories2,
.client-stories3{
  .item-wrap{
    background-color: transparent !important;
  }
  .read-link{
    color: #26D6F1 !important;
    background-image: url('data:image/svg+xml,<svg width="16" height="16" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M14.7643 8.70714C15.1548 8.31662 15.1548 7.68345 14.7643 7.29293L8.40034 0.928951C8.00982 0.538425 7.37665 0.538424 6.98613 0.928947C6.5956 1.31947 6.5956 1.95264 6.98612 2.34316L12.643 8.00003L6.98609 13.6569C6.59557 14.0474 6.59556 14.6806 6.98609 15.0711C7.37661 15.4616 8.00978 15.4616 8.4003 15.0711L14.7643 8.70714ZM-2.67679e-06 9L14.0572 9.00003L14.0572 7.00003L2.67679e-06 7L-2.67679e-06 9Z" fill="%2326D6F1"/></svg>') !important;
  }
}

.client-stories3{
  background-image: url(../img/clients-bg.svg);
  background-size: cover;
  background-position: center;
  .center-grid .title:after{
    background-image: url(../img/whiter-arrow-right.svg);
  }
  .client-story-list .mCSB_scrollTools_horizontal .mCSB_dragger .mCSB_dragger_bar{
    background: rgba(255, 255, 255, 0.40);
  }
  .client-story-list .mCSB_scrollTools_horizontal .mCSB_draggerContainer{
    background: rgba(255, 255, 255, 0.10);
  }
}

.story-card{
  &__top,
  &__title{
    position: relative;
    z-index: 3;
  }
  &__top{
    max-width: 180px;
    min-height: 50px;
    margin-bottom: 28px;
  }
  &__img{
    position: absolute;
    bottom: 0;
    right: 0;
    height: 100%;
    z-index: 1;
  }
}

.text46{
  font-size: 46px;
  line-height: 100%;
  font-weight: 600;
  letter-spacing: 0.46px;
}

.text34{
  font-size: 34px;
  line-height: 118%;
}

.text28{
  font-size: 28px;
}

.semi-bold-text{
  font-weight: 600;
}

.medium-text{
  font-weight: 500;
}

.client-stories2{
  background: #EEFCFE;
  .center-grid .title{
    color: #1D1D1B;
    &:after{
      display: none;
    }
  }
  .read-link{
    position: static !important;
    margin-top: 16px;
  }
  .client-story-list .mCSB_scrollTools_horizontal .mCSB_dragger .mCSB_dragger_bar{
    background: #26D6F1;
  }
  .client-story-list .mCSB_scrollTools_horizontal .mCSB_draggerContainer{
    background: rgba(16, 48, 95, 0.10);
  }
}

.solution-card{
  .item{
    padding-bottom: 40px !important;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__icon{
    width: 80px;
    height: 80px;
    background: #26D6F1;
    @include center;
    margin-bottom: 36px;
    img{
      width: 40px;
    }
  }
}

.rent{
  &__inner{
    padding: 150px 116px;
    background-image: url(../img/rent-bg.webp);
    background-size: cover;
    background-position: center;
    position: relative;
    color: #fff;
    &:before,
    &:after{
      content: '';
      position: absolute;
      width: 85px;
      height: 85px;
      border: 10px solid #fff;
    }
    &:before{
      left: 30px;
      bottom: 30px;
      border-top: none;
      border-right: none;
    }
    &:after{
      right: 30px;
      top: 30px;
      border-bottom: none;
      border-left: none;
    }
  }
  &__content{
    max-width: 614px;
    position: relative;
    z-index: 2;
  }
  &__title{
    font-size: 64px;
    line-height: 122%;
    font-weight: 600;
    letter-spacing: 0.64px;
    margin-bottom: 29px;
  }
  &__nav{
    display: flex;
    margin-top: 40px;
    .m-btn{
      @include lastM(0 24px 0 0);
    }
  }
  &__img{
    position: absolute;
    bottom: 0;
    right: 0;
    max-width: 68%;
  }
  &__img2{
    display: none;
  }
}

.m-btn{
  height: 56px;
  padding: 0 38px;
  font-weight: 500;
  font-size: 18px;
  line-height: 21px;
  transition: all .3s;
  @include center;
  cursor: pointer;
  white-space: nowrap;
  border: none;
  outline: none;
}

.m-btn-red {
  background-color: #ED1C24;
  color: #fff;
  &:hover {
    background: darken(#ED1C24,10%);
    color: #fff;
  }
}

.m-btn-white {
  border: 1px solid #fff;
  color: #fff;
  &:hover {
    background: #fff;
    color: #1f0f63
  }
}

.m-btn-blue-outline {
  border: 1px solid #1f0f63;
  color: #1f0f63;
  &:hover {
    background: #1f0f63;
    color: #fff
  }
}

.rent-info{
  padding: 160px 0;
  &__inner{
    padding: 0 140px;
    display: grid;
    grid-template-columns: 41% 49%;
    justify-content: space-between;
  }
  &__title{
    font-size: 46px;
    line-height: 114%;
    font-weight: 600;
  }
  &__desc{
    line-height: 140%;
  }
}

.free{
  padding-top: 323px;
  color: #fff;
  &__bottom{
    background: #EEFCFE;
    padding-bottom: 170px;
  }
  &__inner{
    background-image: url(../img/free-bg.webp);
    background-size: cover;
    background-position: center;
    padding: 110px 140px 130px;
    transform: translateY(-323px);
    position: relative;
  }
  &__content{
    max-width: 420px;
    position: relative;
    z-index: 2;
  }
  &__title{
    max-width: 415px;
    margin-bottom: 36px;
  }
  &__nav{
    display: flex;
    margin-top: 65px;
  }
  &__img{
    position: absolute;
    right: 0;
    top: 0;
    display: block;
    width: 899px;
    &._view2,
    &._view3,
    &._view4{
      display: none;
    }
  }
}

.questions{
  background: #10305F;
  &__wrap{
    transform: translateY(-178px);
    position: relative;
    &:before{
      content: '';
      position: absolute;
      left: 8px;
      bottom: 8px;
      width: 100%;
      height: 100%;
      background: #26D6F1;
      pointer-events: none;
    }
  }
  &__inner{
    background-color: #fff;
    border: 1px solid #EDEFF3;
    padding: 110px 140px;
    position: relative;
    z-index: 2;
    display: flex;
    justify-content: space-between;
  }
  &__left{
    width: 440px;
  }
  &__nav{
    display: flex;
    margin-top: 40px;
  }
  &__right{
    width: 614px;
  }
  &__subtitle{
    color: #26D6F1;
  }
  &__links{
    margin-top: 16px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__link{
    display: flex;
    align-items: center;
    font-size: 28px;
    line-height: 115%;
    @include lastM(0 0 20px);
    img{
      margin-left: 16px;
      width: 36px;
      flex-shrink: 0;
    }
    &:hover{
      color: #26D6F1;
    }
  }
}

.calc{
  padding: 320px 0 160px;
  position: relative;
  overflow: hidden;
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 1011px;
    background: #EEFCFE;
  }
  &__inner{
    position: relative;
    z-index: 2;
  }
  &__img{
    width: 873px;
    position: absolute;
    right: -111px;
    top: -232px;
  }
  &__grid{
    position: relative;
    z-index: 2;
    background-color: #fff;
    margin-top: 65px;
    display: grid;
    grid-template-columns: calc(100% - 530px) 530px;
  }
  &__content{
    padding: 54px;
    max-width: 1000px;
  }
}

.calc-group{
  @include lastM(0 0 48px);
  .text34{
    margin-bottom: 32px;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 26px));
    gap: 26px 52px;
  }
  &__subtitle{
    font-size: 20px;
    line-height: 140%;
    font-weight: 500;
    margin-bottom: 24px;
  }
  &__radios{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 12px));
    gap: 12px 24px;
  }
  &__col{
    padding-bottom: 24px;
    border-bottom: 1px solid #EDEFF3;
  }
}

.calc-item{
  display: grid;
  grid-template-columns: calc(100% - 160px) 144px;
  gap: 16px;
  align-items: center;
  &__title{
    font-size: 18px;
    line-height: 137%;
    font-weight: 500;
  }
}

.cartcalc{
  max-width: 144px;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(3,calc(100% / 3));
  border: 1px solid #CACDDF;
  height: 48px;
  input,
  button{
    width: 100%;
    height: 100%;
    outline: none !important;
  }
  button{
    border: none;
    background-color: transparent;
    @include center;
    padding: 0;
    svg{
      width: 16px;
      height: auto;
    }
    &:hover{
      cursor: pointer;
      path{
        stroke: #ED1C24;
      }
    }
  }
  input{
    border: 1px solid #CACDDF;
    border-top: none;
    border-bottom: none;
    text-align: center;
    font-size: 16px;
    font-weight: 500;
  }
}

.m-radio{
  position: relative;
  input{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
    cursor: pointer;
    opacity: 0;
    margin: 0;
    &:checked+label:after{
      opacity: 1;
    }
  }
  label{
    display: flex;
    font-size: 16px;
    line-height: 137%;
    font-weight: 500;
    color: #949494;
    position: relative;
    &:before,
    &:after{
      content: '';
      transform: translateY(3px);
    }
    &:before{
      flex-shrink: 0;
      width: 16px;
      height: 16px;
      border: 1px solid #CACDDF;
      margin-right: 12px;
    }
    &:after{
      position: absolute;
      width: 8px;
      height: 8px;
      left: 4px;
      top: 4px;
      background-color: #26D6F1;
      transition: all .3s;
      opacity: 0;
    }
  }
}

.additional-services{
  @include grid100gap(46px);
  &__group{
    display: grid;
    grid-template-columns: 40px calc(100% - 64px);
    gap: 24px;
  }
  &__content{
    padding-bottom: 24px;
    border-bottom: 1px solid #EDEFF3;
  }
  &__check{
    input{
      &:checked+label:before{
        background: #26D6F1;
      }
    }
    label{
      &:before{
        width: 40px;
        height: 40px;
        background-color: #fff;
        border: 8px solid #F2F4FF;
        transition: all .4s;
      }
      &:after{
        display: none;
      }
    }
  }
  .calc-group__subtitle{
    margin-bottom: 12px;
  }
  .calc-item{
    max-width: 364px;
    &__title{
      color: #949494;
    }
  }
}

.calc-sidebar{
  border-left: 1px solid #EDEFF3;
  padding: 54px;
  &__title{
    font-size: 24px;
    line-height: 142%;
    font-weight: 500;
  }
  &__bottom{
    margin-top: 48px;
  }
  &__btn{
    width: 100%;
    margin-top: 30px;
  }
  &__hint{
    font-size: 14px;
    line-height: 129%;
    letter-spacing: 0.28px;
    margin-top: 12px;
  }
}

.selected-props{
  margin-top: 20px;
  @include grid100gap(20px);
  &__group{
    @include grid100gap(20px);
    padding-bottom: 20px;
    border-bottom: 1px solid #EDEFF3;
  }
  &__item{
    display: flex;
    justify-content: space-between;
  }
  &__title,
  &__desc{
    font-size: 16px;
    line-height: 138%;
    font-weight: 500;
  }
  &__title{
    max-width: 200px;
  }
  &__desc{
    text-align: right;
    width: 135px;
    flex-shrink: 0;
    color: #949494;
  }
}


