      gap: 24px;
    }
    &__col{
      padding-bottom: 20px;
    }
    &__radios{
      grid-template-columns: repeat(2,calc(50% - 6px));
      gap: 12px;
    }
    &__subtitle{
      font-size: 16px;
      line-height: 175%;
      margin-bottom: 12px;
    }
  }

  .calc-item{
    &__title{
      font-size: 14px;
    }
  }

  .m-radio{
    label{
      font-size: 14px;
      line-height: 22px;
    }
  }

  .additional-services{
    gap: 20px;
  }

  .calc-sidebar{
    grid-template-columns: 100%;
    &__title{
      font-size: 22px;
      line-height: 154%;
    }
    &__btn{
      margin-top: 20px;
    }
    &_hint{
      margin-top: 20px;
    }
  }

  .selected-props{
    &__title{
      flex-shrink: 0;
    }
  }
}


@media screen and (max-width: 576px){
  .rent{
    &__inner{
      background-image: url(../img/rent-bg2.webp);
    }
    &__title{
      font-size: 32px;
      max-width: 277px;
    }
    .text34{
      max-width: 210px;
    }
    &__nav{
      flex-direction: column;
      .m-btn{
        width: 100%;
        padding: 0;
        @include lastM(0 0 12px);
      }
    }
  }

  .text34{
    font-size: 16px;
  }

  .free{
    &__desc{
      font-size: 18px;
    }
    &__nav{
      .m-btn{
        width: 100%;
        height: 56px;
        font-size: 18px;
      }
    }
  }

  .selected-props{
    &__title,
    &__desc{
      font-size: 14px;
    }
  }
}
